<template>
  <div>
    <v-tabs v-model="tab"
            grow>
      <v-tab v-for="item in tablist"
             class="text-capitalize"
             :key="item.name">
        {{ item.name }}
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item v-for="item in tablist"
                  :key="item.name">
        <keep-alive>
          <component :is="item.component"></component>
        </keep-alive>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import mailBox from './mailBox'
import baseData from './baseData'
import companyAttr from './companyAttr'
import exchange from './exchange'
import timeLanguage from './time_lang_set'
import unit from './unit'
export default {
  // components: {mailBox,baseData,companyAttr,exchange,timeLanguage,unit},
  props: {},
  data () {
    return {
      tab: 0,

    };
  },
  created () {
    this.tab = this.$store.state.tab
  },
  mounted () {
    this.$route.meta.title = this.$store.getters.subPage_name
  },
  methods: {

  },
  computed: {
    tablist () {
      return [
        { name: this.$t('label_The_unit_set'), component: unit },
        { name: this.$t('label_Basic_information'), component: baseData },
        { name: this.$t('label_Time_zone_and_language_Settings'), component: timeLanguage },
        { name: this.$t('label_Exchange_rate_is_set'), component: exchange },
        { name: this.$t('label_Mailbox'), component: mailBox },
        { name: this.$t('label_Company_attribute_configuration'), component: companyAttr },
      ]
    }
  },
  watch: {
    tab (val) {
      this.$store.commit('setTab', val)
    }
  },
  beforeDestroy () {
    this.$store.commit('setTab', '')
  }
};
</script>

