<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div>
      <mrc_ess_iconGroup style="top: -50px"
                         :is_disabled="disabled"
                         :showEdit="hasBtn('edit-company-setting')"
                         :show_del="hasBtn('delete-company-setting')"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>
      <v-card>
        <v-card-text>
          <v-row dense>
            <!-- <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            :label="$t('label_company_name') + '*'"
                            append-icon="mdi-longitude"
                            :rules="$text_rules('单位名称')"
                            v-model="params.org_level_name"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            append-icon="mdi-longitude"
                            :rules="$text_rules('短编码')"
                            :label="$t('label_Short_code') + '*'"
                            v-model="params.org_level_shortcode"></v-text-field>
            </v-col> -->
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :readonly="disabled"
                                 :isNational="judegNational('org_level_name')"
                                 style="width:100%"
                                 :label="$t('label_company_name') + '*'"
                                 append-icon="mdi-longitude"
                                 :rules="$text_rules('单位名称')"
                                 v-model="params.org_level_name"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :readonly="disabled"
                                 :isNational="judegNational('org_level_shortcode')"
                                 style="width:100%"
                                 append-icon="mdi-longitude"
                                 :rules="$text_rules('短编码')"
                                 :label="$t('label_Short_code') + '*'"
                                 v-model="params.org_level_shortcode"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_prompt :rules="false"
                              :disabled='disabled'
                              :label="$t('label_The_higher_the_unit') + '*'"
                              :obj="companyParent"
                              v-model="params.org_level_code_parent">
              </mrc_ess_prompt>
            </v-col>
            <v-card-text>
              <v-col class="d-flex"
                     cols="12"
                     sm="12">
                <v-text-field :label="$t('label_The_unit_name') + '*'"
                              v-model="params.org_level_all"
                              disabled></v-text-field>
              </v-col>
            </v-card-text>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_prompt :rules="false"
                              :disabled='disabled'
                              :label="$t('label_head') + '*'"
                              obj='staffs'
                              v-model="params.org_level_leader_uuid">
              </mrc_ess_prompt>
            </v-col>
          </v-row>
        </v-card-text>
        <mrc_ess_submitBtn @cancel='cancel'
                           v-if="!disabled"
                           @success='success'></mrc_ess_submitBtn>
      </v-card>
    </div>
  </mrc_ess_skLoaders>
</template>

<script>
import { get_company_info, get_company_update, get_company_del } from '@/service/api/company_set'
export default {
  components: {},
  props: {},
  data () {
    return {
      skLoading: true,
      disabled: true,
      params: {
        uuid: '',
        org_level_name: '',
        org_level_shortcode: '',
        org_level_leader_uuid: '',
        org_level_code_parent: '',
        org_level_all: ''
      }
    };
  },
  created () { },
  async mounted () {
    if (!this.$store.state.iconstatus) {
      this.disabled = false
    }
    await this.national('mrbase_sys_org_levels')
    this.skLoading = true
    this.$nextTick(async () => {
      await this.get_info()
    })
    this.skLoading = false
  },
  methods: {
    // 获取详情
    async get_info () {
      const info = await get_company_info({ uuid: this.$store.state.uuid })
      if (info.code == 200) {
        this.params = info.data
        this.$store.commit('set_uuid2', this.params.client_uuid)
      }
    },
    cancel () {
      this.$myConfirm({ title: '提示', content: '是否放弃修改' })
        .then(async () => {
          await this.get_info()
          this.disabled = !this.disabled
        }).catch(() => { })
    },
    async success () {
      const update = await get_company_update({
        uuid: this.params.uuid,
        org_level_name: this.params.org_level_name,
        org_level_shortcode: this.params.org_level_shortcode,
        org_level_code_parent: this.params.org_level_code_parent,
        org_level_leader_uuid: this.params.org_level_leader_uuid,
        client_uuid: this.$store.state.currentCompanyClientUuid
      })
      if (update.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
        this.disabled = !this.disabled
        await this.get_info()
        await this.$store.dispatch('set_topcompanylist')
      }
    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除
    handleDel () {
      this.$myConfirm({ title: '提示', content: '是否删除' })
        .then(async () => {
          const del = await get_company_del({
            uuid: this.params.uuid
          })
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.go(-1)
          }
        }).catch(() => { })
    },
  },
  computed: {
    companyParent () {
      // return {prompt_name: 'change-org',org_level_uuid: this.$store.state.uuid}
      return { prompt_name: 'parent_org_level', org_level_code: this.$store.state.orgLevelCode }
    }
  },
  watch: {},
};
</script>

<style scoped>
</style>
